<template>
  <div class="modal">
    <img :src='image_src' alt="">
    <div class="border">
      <h1>{{info.title}}</h1>
      <p>{{info.desc}}</p>

      <div class="buttons-div">
        <button class="btn-arrow-left" type="button" name="button" @click='$emit("prev")'></button>
        <button class="btn-grid" type="button" name="button" @click='$emit("click")'></button>
        <button class="btn-arrow-right" type="button" name="button" @click='$emit("next")'></button>
      </div>
    </div>
    <button class="btn-close" type="button" name="button" @click='$emit("click")'></button>
  </div>
</template>

<script>
import Swipe from './../Swipe.vue';
export default {
  mixins:[Swipe],
  props:["info", "value"],
  data(){
    return{

    }
  },
  watch:{
    touch:function(val){
      switch (val) {
        case "left":
          this.$emit("prev");
          break;
        case "right":
          this.$emit("next");
          break;
      }
      this.touch = null;
    }
  },
  methods:{
    keypress:function(event){
      if(event.code === "ArrowRight")
        this.$emit("next");

      if(event.code === "ArrowLeft")
        this.$emit("prev");
    }
  },
  computed:{
    image_src:function(){
      var images = require.context('./../../assets/icons', false)
      return images("./" + this.info.img)
    }
  },
  mounted:function(){
    window.addEventListener('keydown', this.keypress);
  }
}
</script>
