<template>
    <canvas id="canvas" :width="width" :height="height"></canvas>
</template>

<script>
import star_img from './../assets/star.svg';
import shooting_star_img from './../assets/shooting-star.svg';
export default {
  props:["width", "height", "perc", "padding_height"],
  name:'Stars',
  data(){
    return{
      vue_canvas: null,
      padding: 0,
      padding_y: (this.padding_height) ? this.padding_height : 0,
      percentage: (this.perc) ? this.perc : 1,
      pos:[],
      star: null,
      shooting_star:{
        img:null,
        pos:{
          x: 1000,
          y: 200,
        },
        width: 130,
        height: 40,
        speed: 24,
        wait: 100,
        wait_time: 100
      },
      CONST:{
        SMALL: 'small',
        MED: 'med',
        LARGE: 'large'
      },
      STATE:{
        GROW: 0,
        DELAY: 1,
        SHRINK: 2
      }
    }
  },
  methods:{
    generate_position:function(ctx){
      const grid_width = 40;
      const grid_height = 40;
      const star_size_max = 40;
      const star_suze_med = 20;
      const star_size_min = 5;
      const star_min = 5;

      let grid_x_count = Math.ceil(ctx.canvas.width / grid_width);
      let grid_y_count = Math.floor(ctx.canvas.height / grid_height);

      let pos = [];

      for(let i = 0; i < grid_x_count + 1; i++){
        for(let j = 0; j < grid_y_count + 1; j++){
          let perc = Math.random();
          let star_size = star_size_min;
          let type = this.CONST.SMALL;

          if(perc < 0.05){
            star_size = star_size_max;
            type = this.CONST.LARGE;
          }
          else if(perc < 0.4){
            star_size = star_suze_med;
            type = this.CONST.MED;
          }

          let size = (Math.random() * star_size) + star_min

          pos.push({
            x: (Math.random() * ((i+1) * grid_width)) + (i * grid_width) - grid_width/2,
            y: (Math.random() * ((j+1) * grid_height)) + (j * grid_height),
            // x: (i * grid_width) + space,
            // y: (j * grid_height) + space,
            size: size,
            size_min: size,
            size_max: size + ((Math.random() * 5) + 1),
            state: this.STATE.GROW,
            type: type,
            speed: (Math.random() * 0.5)
          });
        }
      }

      return pos;
    },
    draw_star:function(){
      this.pos.forEach((item) => {
        if(item.type !== this.CONST.SMALL){
          switch (item.state) {
            case this.STATE.GROW:
              if(item.size < (item.size_max))
                 item.size = (item.size + item.speed);
              else
                item.state = this.STATE.SHRINK;
            break;
            case this.STATE.DELAY:
              item.size = item.size_min;
              if(Math.random() < 0.01)
                item.state = this.STATE.GROW;
            break;
            case this.STATE.SHRINK:
              if(item.size > (item.size_min))
                 item.size = (item.size - item.speed);
              else
                item.state = this.STATE.DELAY;
            break;
          }
        }
        this.vue_canvas.drawImage(this.star, item.x, item.y, item.size, item.size);
      });

      if(this.shooting_star.pos.x > -this.shooting_star.width){
        this.shooting_star.pos.x = Number(this.shooting_star.pos.x) - this.shooting_star.speed;
        this.shooting_star.pos.y += 1;
      }
      else{
        if(this.shooting_star.wait > 0){
          this.shooting_star.wait -= 1;
        }
        else{
          this.shooting_star.wait = this.shooting_star.wait_time + ((Math.random() * this.shooting_star.wait_time) * 2);
          this.shooting_star.pos.x = this.vue_canvas.canvas.width;
          this.shooting_star.pos.y = 200;
        }
      }

      this.vue_canvas.drawImage(this.shooting_star.img, this.shooting_star.pos.x, this.shooting_star.pos.y, this.shooting_star.width, this.shooting_star.height);
    },
    animate:function(){
      this.vue_canvas.clearRect(0, 0, this.vue_canvas.canvas.width, this.vue_canvas.canvas.height);
      this.draw_star()
      window.requestAnimationFrame(this.animate);
    },
    redraw:function(){
      this.vue_canvas.canvas.width  = (window.innerWidth * this.percentage) - this.padding;
      this.vue_canvas.canvas.height = (window.innerHeight) - this.padding_y;
      this.pos = this.generate_position(this.vue_canvas);
      this.draw_star(this.vue_canvas, this.pos);
      this.animate(this.vue_canvas, this.pos);
    },
    load:function(){
      var c = document.getElementById("canvas");
      var ctx = c.getContext("2d");
      ctx.canvas.width = 1080;
      ctx.canvas.height = 1080;

      ctx.canvas.width  = (window.innerWidth) * this.percentage;
      ctx.canvas.height = (window.innerHeight) - this.padding_y;
      this.shooting_star.pos.x = ctx.canvas.width;

      return ctx;
    },
  },
  mounted:function(){
    console.log("star mounted");
    this.star = new Image();
    this.star.src = star_img;

    this.shooting_star.img = new Image();
    this.shooting_star.img.src = shooting_star_img;

    this.vue_canvas = this.load();
    this.pos = this.generate_position(this.vue_canvas);
    this.draw_star(this.vue_canvas, this.pos);
    this.animate();

  //  window.addEventListener("resize", this.redraw);
  },
  beforeDestroy:function(){
    window.removeEventListener("resize", this.redraw);
  }
}
</script>
