<template>
  <div class="modal">
    <img :src='image_src' alt="">
    <div class="border">
      <h1>{{pages[index].title}}</h1>
      <p>{{pages[index].desc}}</p>
      <div class="">
        <button type="button" name="button"></button>
      </div>
      <button v-show='(this.scrollable)' class="btn-scroll" type="button" name="button" @click='scroll()'>SCROLL DOWN</button>
      <Pagination :size='pages.length' v-model='index'></Pagination>
    </div>
    <button class="btn-close" type="button" name="button" @click='$emit("click")'></button>
  </div>
</template>

<script>
import Pagination from './../Pagination.vue';
export default {
  props:["pages"],
  data(){
    return{
      index: 0
    }
  },
  methods:{
    scroll:function(){
      if(this.scrollable)
        this.index += 1;
    },
    page_down:function(){
      if(this.scrollable)
        this.index += 1;
    },
    page_up:function(){
      if((this.index - 1) >= 0)
        this.index -= 1;
    },
    setIndex:function(index){
      this.index = index;
    },
    mouse_scroll:function(event){
      if(event.deltaY > 0)
        this.page_down();
      else
        this.page_up();
    },
    keypress:function(event){
      if(event.code === "ArrowDown")
        this.page_down();

      if(event.code === "ArrowUp")
        this.page_up();
    }
  },
  computed:{
    scrollable:function(){
      return (this.index < this.pages.length - 1);
    },
    image_src:function(){
      if(this.pages[this.index].img){
        var images = require.context('./../../assets/icons', false)
        return images("./" + this.pages[this.index].img)
      }

      return '';
    }
  },
  mounted:function(){
    window.addEventListener('wheel', this.mouse_scroll);
    window.addEventListener('keydown', this.keypress);
    this.index = 0;
  },
  created:function(){

  },
  components:{
    Pagination
  }
}
</script>
