<template>
  <div class="code-page">
    <div class="code-prev-div" v-if='value == CONST.CODE'>
      <span>{{info_data.title}}</span>
      <p>{{info_data.desc}}</p>
    </div>
    <div :class='"circle anim-rotate-left " + animation'>
      <transition name="rotate-over">
        <Radial v-if='value == CONST.CODE' @onClick='showProject' v-model='project_index'></Radial>
      </transition>
      <div class="circle-inner anim-rotate-right">
        <div class="circle-inner anim-rotate-left-2">
        </div>
      </div>
    </div>
    <transition name="slide">
      <Info v-if='show_info' :pages='info_data.pages' @click='clear()'></Info>
    </transition>
  </div>
</template>

<script>
import Info from './../components/modal/modal-project-details.vue';
import Radial from './../components/Radial.vue';
import CodeJSON from './../json/code.json';
export default {
  props:["value"],
  data(){
    return{
      options: CodeJSON,
      show_info: false,
      info_data: null,
      project_index: 1,
      CONST:{
        HOME: "home",
        ART: "art",
        CODE: "code",
        ABOUT: "about"
      },
    }
  },
  watch:{
    project_index:function(val){
      this.info_data = this.options[val];
    }
  },
  computed:{
    animation:function(){
      switch (this.value) {
        case this.CONST.ABOUT:
          return "about-slide-down";
      }

      return "about-slide-up";
    }
  },
  methods:{
    showProject:function(index){
      if(index == null){
        this.$emit("setView", this.CONST.HOME);
      }
      else{
        this.show_info = true;
        this.info_data = this.options.find((item) => item.id == index);
      }
    },
    clear:function(){
      this.show_info = false;
      // this.info_data = null
    }
  },
  created:function(){
    this.info_data = this.options[this.project_index];
  },
  components:{
    Radial,
    Info
  }
}
</script>
