<template>
  <div class="pagination-div">
    <div>
      <hr>
      <button
        v-for='(size, i) in size'
        v-bind:key='i'
        :class='_style(i)'
        type="button"
        name="button"
        @click='setIndex(i)'
      >
      </button>
    </div>
  </div>
</template>

<script>
export default {
  props:["size", "value"],
  data(){
    return{}
  },
  methods:{
    setIndex(index){
      this.$emit('input', index);
    },
    _style:function(c_index){
      return (this.value == c_index) ? "active" : "";
    }
  },
  computed:{
    
  }
}
</script>
