<template>
    <div class="home">
      <Stars :width='star_canvas_width' :height='star_canvas_height'></Stars>
      <Code v-model='value' @setView='setView'></Code>
      <div :class="'libra-div ' + value">
        <button class="libra-btn" @click='setView(CONST.ABOUT)'></button>
        <div class="libra-buttons-div">
          <button type="button" class="btn-code" name="button" @click='setView(CONST.CODE)'>CODE</button>
          <button type="button" class="btn-art" name="button" @click="setView(CONST.ART)">ART</button>
        </div>
      </div>
    </div>
</template>

<script>
import Stars from './../components/Stars.vue';
import Code from './Code.vue';

export default {
  props:['value'],
  name: 'Home',
  data(){
    return{
      CONST:{
        HOME: "home",
        ART: "art",
        CODE: "code",
        ABOUT: "about"
      },
      vue_canvas: null,
      padding: 0,
      star_canvas_width: window.innerWidth,
      star_canvas_height: window.innerHeight
    }
  },
  computed:{
    animation:function(){
      switch (this.value) {
        case this.CONST.ABOUT:
          return "about-slide-down";
      }

      return "about-slide-up";
    }
  },
  methods:{
    setView:function(view){
      this.$emit("setView", view);
    }
  },
  mounted:function(){},
  components: {
    Stars,
    Code
  }
}
</script>
