<template>
  <div class="page">
    <div class="art-circle"></div>
    <span class="header mb-3">ART</span>
      <!-- <div class="tabs">
        <label v-for='tab in tabs' v-bind:key='tab.tag' :for="tab.text">
          <input type="checkbox" :id='tab.text' :name="tab.text" :value="tab.tag" v-model="tag">
          <span>{{tab.text}}</span>
        </label>
      </div> -->
      <!-- <hr> -->
    <Gallery v-if='!show_info' :tag='tag' @click='show' :options='options' :column='5'></Gallery>
    <transition name="slide">
      <Info
        v-if='show_info'
        :info='info_data'
        @next='next()'
        @prev='prev()'
        @click='clear()'
      ></Info>
    </transition>
    <!-- <hr> -->
  </div>
</template>

<script>
import Gallery from './../components/gallery/Gallery.vue';
import Info from './../components/modal/modal-art-details.vue';
import ArtJSON from './../json/art.json';

export default {
  props:[],
  data(){
    return{
      options: ArtJSON,
      tag_lookup:[
        {text:"C#", tag:"c#"},
        {text:"Vue JS", tag:"vue"},
        {text:"Unity 3D", tag:"unity"},
        {text:"Javascript", tag:"javascript"}
      ],
      tag:[],
      show_info: false,
      info_data: null
    }
  },
  computed:{
    tabs:function(){
      let tab_list = this.options.flatMap((item) => item.tag);
      return this.tag_lookup.filter((item) => tab_list.includes( item.tag ) );
    }
  },
  methods:{
    clear:function(){
      this.show_info = false;
      this.info_data = null
    },
    show:function(index){
      this.show_info = true;
      this.info_data = this.options.find((item) => item.id == index);
    },
    next:function(){
      let index = this.options.findIndex((item) => item.id == this.info_data.id);
      if(index + 1 < this.options.length){
        this.info_data = this.options[index + 1];
      }
      else{
        this.show_info = false;
      }
    },
    prev:function(){
      let index = this.options.findIndex((item) => item.id == this.info_data.id);
      if(index - 1 >= 0){
        this.info_data = this.options[index - 1];
      }
      else{
        this.show_info = false;
      }
    }
  },
  components:{
    Gallery,
    Info
  }
}
</script>
