<template>
  <div class="navigation-circle-div">
    <button class="half-circle btn-menu" type="button" name="button" @click="return_home">
      <!-- <img :src="image_src" alt=""> -->
      <span></span>
    </button>
    <Navigation v-model="show"></Navigation>
    <span class="half-circle btn-menu-under"></span>
  </div>
</template>

<script>
import Navigation from './Navigation.vue';

export default {
  data(){
    return{
      show: false
    }
  },
  methods:{
    return_home:function(){
      this.$emit("input", "home");
    }
  },
  computed:{
    image_src:function(){
      var images = require.context('./../assets/', false)
      return images("./bunny-small.svg")
    },
  },
  components:{
    Navigation
  }
}
</script>
