<template>
  <div id="app" :class='scollabe'>
    <div class="outer-border">
      <div class="base-dark">
        <Home @setView='setView' v-model='VIEW'></Home>
      </div>
    </div>
    <!-- <div v-show='VIEW != CONST.ABOUT' :class="'slide-up-page slide-y-animation ' + VIEW"> -->
    <div :class="'slide-up-page slide-y-animation ' + VIEW">
      <div class="cloud-background">
          <div class="cloud"></div>
          <div class="cloud-fill"></div>
          <div class="cloud-large"></div>
      </div>
      <Navigation v-show='VIEW == CONST.ART' v-model='VIEW'></Navigation>
      <div class="cloud-art">
        <Art></Art>
      </div>
    </div>
    <transition name="slide-about">
      <About v-show='VIEW == CONST.ABOUT' v-model='VIEW'></About>
    </transition>

    <!-- <strong class="copyright">Copyright © 2022 Aileen Zhou. All Rights Reserved</strong> -->
    <Loading v-model='loading' v-show='loading'></Loading>
    <Temp v-if='index'></Temp>
  </div>
</template>

<script>
import Keyboard from './components/Keyboard.vue';
import Loading from './components/Loading.vue';
import Navigation from './components/navigation-circle.vue';
import Art from './views/Art-2.vue';
import Home from './views/Home.vue';
import About from './views/About.vue';
import Temp from './views/Temp.vue';
export default{
  mixins:[Keyboard],
  data(){
    return{
      index:true,
      loading: true,
      show: false,
      CONST:{
        HOME: "home",
        ART: "art",
        CODE: "code",
        ABOUT: "about"
      },
      VIEW: "home"
    }
  },
  watch:{
    keyboard:function(val){
      if(this.CONST.HOME == this.VIEW)
      {
        switch (val) {
          case "left":
          break;
          case "right":
          this.setView(this.CONST.CODE);
          break;
          case "up":
          this.setView(this.CONST.ART);
          break;
          case "down":
          this.setView(this.CONST.ABOUT);
          break;
        }
      }

      val = null;
    }
  },
  methods:{
    home:function(){
      this.$router.push({ path: '/' })
    },
    setView:function(view){
      this.VIEW = view;
    }
  },
  computed:{
    scollabe:function(){
      return (this.VIEW != this.CONST.ART || this.show) ? "no-scroll" : "";
    },
  },
  mounted:function(){
    if(this.$router.history.current.name == "Home"){
      this.index = false;
    }
  },
  components: {
    Navigation,
    Loading,
    Art,
    Home,
    About,
    Temp
  }
}
</script>
